import shares from '@/components/share';
import share from "@/common/share.js";
import {
    mapState 
} from 'vuex';  
import uniRequest from 'uni-request';
import minBadge from '@/components/min-badge/min-badge'
import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue"
export default{
	components: {
		shares,
		uniNumberBox,
		minBadge
	},
	
	data() {
		return {
			specClass: 'none',
			specSelected:[],
			detailsArr:{},
			favorite: true,
			shareList: [],
			imgList: [],
			subText:'完成',
			count:1,
			value: 1,
			min:0,
			max:3,
			specList: [],
			specChildList: [],
			sku_id:'',
			num:0,
			results:[],
			username:uni.getStorageSync('userInfo').username,
			headPicValue:'',
			score:0,
			shareObj:{
				shareMenu:''
			},
			
		};
	},
	
	filters: {
		/**
		 * 处理富文本里的图片宽度自适应
		 * 1.去掉img标签里的style、width、height属性
		 * 2.img标签添加style属性：max-width:100%;height:auto
		 * 3.修改所有style里的width属性为max-width:100%
		 * 4.去掉<br/>标签
		 * @param html
		 * @returns {void|string|*}
		 */
		formatRichText (html) { //控制小程序中图片大小
			let newContent= html.replace(/<img[^>]*>/gi,function(match,capture){
				match = match.replace(/style="[^"]+"/gi, '').replace(/style='[^']+'/gi, '');
				match = match.replace(/width="[^"]+"/gi, '').replace(/width='[^']+'/gi, '');
				match = match.replace(/height="[^"]+"/gi, '').replace(/height='[^']+'/gi, '');
				return match;
			});
			newContent = newContent.replace(/style="[^"]+"/gi,function(match,capture){
				match = match.replace(/width:[^;]+;/gi, 'max-width:100%;').replace(/width:[^;]+;/gi, 'max-width:100%;');
				return match;
			});
			newContent = newContent.replace(/<br[^>]*\/>/gi, '');
			newContent = newContent.replace(/\<img/gi, '<img style="max-width:100%;height:auto;display:inline-block;margin:10rpx auto;"');
			return newContent;
		}	
	},
	
	onShow(){
		// 
		if(uni.getStorageSync('userInfo').token){
			this.getShopCount()
			if(uni.getStorageSync('userInfo').code){
				this.headPicValue = 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1570534827025&di=11e89dd29f4faec93dd0de848fb379cc&imgtype=0&src=http%3A%2F%2Fimg.zcool.cn%2Fcommunity%2F01c8415b10ec7aa801212d57334fdc.png%401280w_1l_2o_100sh.png'
			}else{
				this.headPicValue = 'http://img.zcool.cn/community/01786557e4a6fa0000018c1bf080ca.png'
			}
		}
	},
	
	onBackPress() {
		//监听back键，关闭弹出菜单
		if (this.shareObj.shareMenu.isVisible()) {
			this.shareObj.shareMenu.hide();
			this.shareObj.alphaBg.hide();
			return true
		}
	},
	async onLoad(options){
		console.log(options)
		
		//接收传值,id里面放的是标题，因为测试数据并没写id 
		let id = options.id;
		this.sku_id = id
		console.log(this.sku_id,id)
		if(id){
			// this.$api.msg(`点击了${id}`);
			this.getDate(id)
			if(uni.getStorageSync('userInfo').token){
				this.getAssess(id)
			}
			this.getShopCount()
		}
		
		//规格 默认选中第一条
		this.specList.forEach(item=>{
			for(let cItem of this.specChildList){
				if(cItem.pid === item.id){
					this.$set(cItem, 'selected', true);
					this.specSelected.push(cItem);
					break; //forEach不能使用break
				}
			}
		})
		this.shareList = await this.$api.json('shareList');
	},
	
	computed: {
		...mapState(['hasLogin','userInfo'])
	},
	methods:{
		async getDate(id){
			try {
				const response = await uniRequest.get('/mobile/get/goods/'+id);
				console.log(response.data)
				this.detailsArr = response.data
				// this.detailsArr.goods = []
				this.imgList = response.data.images
			} catch (error) {
				console.error(error);
			}
		},
		
		async getAssess(id){
			const response = await uniRequest({
				url:'/goods/comment/?id='+id+'&page_size=5',
				method:'get',
				headers:{
					Authorization:'JWT '+uni.getStorageSync('userInfo').token
				},
			}).then(res=>{
				if(res.status === 200){
					this.results = res.data.results
					var score = 0
					this.results.forEach(ele=>{
										   score += ele.score * 1/5
					})
					
					this.score = Math.round(score/this.results.length*100) + '%'
				}
			   
			}).catch(error=>{
			   console.log(error)
			})
		},
		
		bindChange(res) {
			this.count = Number(res)
			console.log('返回数据',res);
		},
		//规格弹窗开关
		toggleSpec() {
			if(this.specClass === 'show'){
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			}else if(this.specClass === 'none'){
				this.specClass = 'show';
			}
			
		},
		async toggleJoin(){
			this.toggleSpec()



















































			
			
			
		},
		async getShopCount(){
			const response = await uniRequest({
				url:'/cart/',
				method:'get',
				headers:{
					Authorization:'JWT '+uni.getStorageSync('userInfo').token,
				},
				crossDomain: true,
				xhrFields: {
					withCredentials: true
				},
			}).then(res=>{
				console.log(res.data)
				this.num = res.data.length
				console.log(this.num)
			}).catch(err=>{
				
			})
			
		},
		async toggleBuy(){
			this.toggleSpec()
			if(!uni.getStorageSync('userInfo').token){
				uni.navigateTo({
					url: `/pages/public/login`
				})
			}else{
				uni.navigateTo({
					url: `/pages/order/createOrder?data=`+JSON.stringify(this.detailsArr)+`&count=`+this.count + `&goods_id=`+ this.sku_id
				})
			}
		},
		
		//选择规格
		selectSpec(index, pid){
			let list = this.specChildList;
			list.forEach(item=>{
				if(item.pid === pid){
					this.$set(item, 'selected', false);
				}
			})

			this.$set(list[index], 'selected', true);
			//存储已选择
			/**
			 * 修复选择规格存储错误
			 * 将这几行代码替换即可
			 * 选择的规格存放在specSelected中
			 */
			this.specSelected = []; 
			list.forEach(item=>{ 
				if(item.selected === true){ 
					this.specSelected.push(item); 
				} 
			})
		},
		
		//分享
		share(){
			this.$refs.share.toggleMask();	
		},
		
		// app分享
		shareInfo(){
			let shareInfo={
				href:"http://m.hfyt365.com/#/indexDetails?id="+this.sku_id,
				title:"行丰商城",
				desc:this.detailsArr.name,
				imgUrl:this.detailsArr.default_image_url
			};
			let shareList=[
				{
					icon:"/static/sharemenu/wx.png",
					text:"微信好友",
				},
				{
					icon:"/static/sharemenu/pyq.png",
					text:"朋友圈"
				},
				{
					icon:"/static/sharemenu/weibo.png",
					text:"微博"
				},
				{
					icon:"/static/sharemenu/qq.png",
					text:"QQ"
				},
				{
					icon:"/static/sharemenu/copy.png",
					text:"复制"
				},
				{
					icon:"/static/sharemenu/more.png",
					text:"更多"
				},
			];
			this.shareObj=share(shareInfo,shareList,function(index){
				console.log("点击按钮的序号: " + index);
				let shareObj={
					href:shareInfo.href||"",
					title:shareInfo.title||"",
					summary:shareInfo.desc||"",
					success:(res)=>{
						console.log("success:" + JSON.stringify(res));
					},
					fail:(err)=>{
						console.log("fail:" + JSON.stringify(err));
					}
				};
				switch (index) {
					case 0:
						shareObj.provider="weixin";
						shareObj.scene="WXSceneSession";
						shareObj.type=0;
						shareObj.imageUrl=shareInfo.imgUrl||"";
						uni.share(shareObj);
						break;
					case 1:
						shareObj.provider="weixin";
						shareObj.scene="WXSenceTimeline";
						shareObj.type=0;
						shareObj.imageUrl=shareInfo.imgUrl||"";
						uni.share(shareObj);
						break;
					case 2:
						shareObj.provider="sinaweibo";
						shareObj.type=0;
						shareObj.imageUrl=shareInfo.imgUrl||"";
						uni.share(shareObj);
						break;
					case 3:
						shareObj.provider="qq";
						shareObj.type=1;
						shareObj.imageUrl=shareInfo.imgUrl||"";
						uni.share(shareObj);
						break;
					case 4:
						uni.setClipboardData({
							data:shareInfo.href,
							complete() {
								uni.showToast({
									title: "已复制到剪贴板"
								})
							}
						})
						break;
					case 5:
						plus.share.sendWithSystem({
							type:"web",
							title:shareInfo.title||"",
							thumbs:[shareInfo.imgUrl||""],
							href:shareInfo.href||"",
							content: shareInfo.desc||"",
						})
						break;
				};
			});
			this.$nextTick(()=>{
				this.shareObj.alphaBg.show();
				this.shareObj.shareMenu.show();
			})
		},
		//收藏
		toFavorite(){






			
		},
		toCart(){
			uni.switchTab({
				url:'/pages/cart/cart',
				success(){
					
				}
			})
		},
		buy(){
			this.toggleSpec()
			this.subText = '立即购买'
		},
		// 加入购物车
		join(){
			this.toggleSpec()
			this.subText = '加入购物车'
		},
		stopPrevent(){
			return
		}
	},

}
